$primary-color: #333;

body {
  font: 100% $font-stack;
  color: $primary-color; }

.menu_ayuda {
	font-weight: 600;
	letter-spacing: 1px;
	padding-top: 20px; }

.header_busqueda {
	input {
		border: none;
